.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;

  width: 25rem;

  @include Raleway400;
  line-height: 140%;
  letter-spacing: 0.02em;
  color: $color-default-black;

  display: flex;
  flex-wrap: wrap;
  column-gap: 2.81rem;
  row-gap: 1.5rem;
}

.main-nav__item {
  position: relative;
}

.main-nav__link {
  font-size: 0.94em;

  @include hover {
    opacity: 0.6;
  }

  &--active {
    @include Raleway700;
    color: #6e9c9f;
  }

  @include vp-767 {
    display: flex;
  }
}

.main-nav__sublist {
  position: absolute;
  left: 0;
  top: 25px;
  margin: 0;
  padding: 0 0.5rem;
  list-style: none;

  display: flex;
  flex-direction: column;
  row-gap: 0.63rem;

  overflow-y: scroll;
  height: 0;
  background-color: #f1eadc;

  &:focus-within {
    height: 8rem;
  }

  @include vp-767 {
    display: none;
  }
}

.main-nav__item--sublist:hover .main-nav__sublist{
  height: 8rem;
  z-index: 2;
}

.main-nav__link--sublist:focus + .main-nav__sublist{
  height: 8rem;
  z-index: 2;
}

.main-nav__sublink {
  font-size: 0.81em;

  @include hover {
    opacity: 0.7;
  }
}

